<template>
  <div id="robot-field">
    <input
      type="checkbox"
      name="robot"
      id="robot"
      aria-required="true"
      data-testid="robot-field--input"
      v-model="input"
    />
    <label for="robot">I am not a robot</label>
  </div>
</template>

<script>
export default {
  name: "RobotField",
  data() {
    return {
      input: "",
      fieldIsValid: false,
    };
  },
  watch: {
    input(value) {
      this.fieldIsValid = value;
      this.$emit("validateRobotField", "robotField", this.fieldIsValid);
    },
  },
};
</script>

<style scoped lang="scss">
#robot-field {
  width: 100%;
  margin-bottom: 30px;
  label {
    padding-left: 10px;
  }
}
</style>
