<template>
  <div class="password-wrapper">
    <label for="password">PASSWORD</label>
    <div id="password-field" :class="{ invalid: !passwordIsValid }">
      <input
        :type="inputFieldType"
        name="password"
        id="password"
        aria-required="true"
        data-testid="password-field--input"
        v-model.lazy="input"
        minLength="6"
        autocomplete="on"
        current-password
      />
      <ToggleButton
        :icon="getIconType()"
        @buttonToggled="togglePasswordVisibility"
      ></ToggleButton>
    </div>
    <div
      v-if="!passwordIsValid"
      class="invalid-password"
      data-testid="password-field--div-invalid"
    >
      Please enter a valid password.
    </div>
  </div>
</template>

<script>
import ToggleButton from "./ToggleButton.vue";
export default {
  name: "PasswordField",
  emits: ["validatePassword"],
  data() {
    return {
      input: "",
      // default to true because user should not be presented with error messages before they enter anything in the input box
      passwordIsValid: true,
      inputFieldType: "password",
    };
  },
  components: {
    ToggleButton,
  },
  watch: {
    input(value) {
      const MIN_PASSWORD_LENGTH = 6;
      if (value && value.length >= MIN_PASSWORD_LENGTH) {
        this.passwordIsValid = true;
      } else {
        this.passwordIsValid = false;
      }
      this.$emit(
        "validatePassword",
        "password",
        this.passwordIsValid && !!this.input.length
      );
    },
  },
  methods: {
    togglePasswordVisibility() {
      if (this.inputFieldType === "text") {
        this.inputFieldType = "password";
      } else {
        this.inputFieldType = "text";
      }
    },
    getIconType() {
      return this.inputFieldType === "password" ? "show" : "hide";
    },
  },
};
</script>

<style scoped lang="scss">
.password-wrapper {
  padding-bottom: 25px;
  label {
    display: block;
    font-size: 11px;
    margin-bottom: 10px;
  }
  #password-field {
    padding: 0;
    margin: 0;
    padding-right: 5px;
    display: flex;
    flex-direction: row;
    border: 1px solid grey;
    height: 30px;
    width: 100%;
    border-radius: 2px;
    :focus {
      border: 1px solid black;
    }

    input {
      flex-grow: 2;
      border: none;
      width: 100%;
    }
    &.invalid {
      border-color: red;
    }
    input:focus {
      outline: none;
    }
  }
  .invalid-password {
    color: red;
  }
}
</style>
