<template>
  <div id="login-form-wrapper">
    <form id="login-form">
      <h1 id="app-header">{{ header }}</h1>
      <EmailField @validate-email="validate" :key="componentKey"></EmailField>
      <PasswordField
        @validate-password="validate"
        :key="componentKey"
      ></PasswordField>
      <RobotField @validate-robot-field="validate"></RobotField>
      <FormControls
        :login-enabled="loginEnabled"
        @login="login"
        @clear-form="clearForm"
      ></FormControls>
    </form>
  </div>
</template>

<script>
import EmailField from "./EmailField.vue";
import PasswordField from "./PasswordField.vue";
import RobotField from "./RobotField.vue";
import FormControls from "./FormControls.vue";
export default {
  name: "LoginForm",
  data() {
    return {
      emailIsValid: false,
      passwordIsValid: false,
      robotFieldIsValid: false,
      componentKey: 0,
    };
  },
  computed: {
    loginEnabled() {
      return (
        this.emailIsValid && this.passwordIsValid && this.robotFieldIsValid
      );
    },
  },
  props: {
    header: String,
  },
  components: {
    EmailField,
    PasswordField,
    RobotField,
    FormControls,
  },
  methods: {
    validate(type, isValid) {
      if (type === "email") this.emailIsValid = isValid;
      if (type === "password") this.passwordIsValid = isValid;
      if (type === "robotField") this.robotFieldIsValid = isValid;
    },
    clearForm() {
      // use key changing to force child component re-render
      this.componentKey += 1;
      this.emailIsValid = false;
      this.passwordIsValid = false;
    },
    login() {
      if (this.loginEnabled) {
        window.confirm("Login successful!");
      }
    },
  },
};
</script>

<style scoped lang="scss">
#login-form-wrapper {
  display: grid;
  align-items: center;
  background-color: #f4f5f7;
  form#login-form {
    height: 400px;
    text-align: left;
    background-color: white;
    margin-right: 40px;
    margin-left: 40px;
    border-radius: 5px;
    padding: 20px;
    box-shadow: 0 4px 2px -2px lightgrey;
    #app-header {
      font-size: 24px;
    }
  }
}
</style>
