<template>
  <div id="email-field">
    <label for="email">EMAIL ADDRESS</label>
    <input
      type="text"
      name="email"
      id="email"
      aria-required="true"
      data-testid="email-field--input"
      v-model.lazy="input"
      :class="{ invalid: !emailIsValid }"
      autocomplete="on"
    />
    <div
      v-if="!emailIsValid"
      class="invalid-email"
      data-testid="email-field--div-invalid"
    >
      Please enter a valid email address.
    </div>
  </div>
</template>

<script>
export default {
  name: "EmailField",
  data() {
    return {
      input: "",
      // default to true because user should not be presented with error messages before they enter anything in the input box
      emailIsValid: true,
    };
  },
  watch: {
    input(value) {
      const validEmailRegEx = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
      if (validEmailRegEx.test(value)) {
        this.emailIsValid = true;
      } else {
        this.emailIsValid = false;
      }
      this.$emit(
        "validateEmail",
        "email",
        this.emailIsValid && !!this.input.length
      );
    },
  },
};
</script>

<style scoped lang="scss">
#email-field {
  width: 100%;
  margin-bottom: 30px;
  label {
    display: block;
    font-size: 11px;
    margin-bottom: 10px;
  }
  input {
    height: 25px;
    width: 100%;
    &.invalid {
      border-color: red;
    }
  }
  .invalid-email {
    color: red;
  }
}
</style>
